/*ResetBootstrap*/
body {
    font-size: 14px;
    font-family: 'PT Sans', sans-serif;
    color: #525252;
}

a {
    color: #00B4D8;

    &:hover,
    &:focus {
        color: #F18E3F;
    }
}

@media(min-width: 1206px) {
    .container {
        max-width: 1176px;
    }
}

/*Header*/
.header-top-section {
    display: flex;
    flex-direction: column;

    @media(max-width: 767px) {
        padding: 7px 0;
    }
}

.header-navigation-subsection {
    @media(min-width: 768px) {
        order: -1;
        border-bottom: 1px solid #D1D1D1;
    }

}

.header-main-navigation {
    ul {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
    }

    &>ul {
        @media(max-width: 767px) {
            padding-top: 5px;
        }

        @media(min-width: 768px) {
            display: flex;
            min-height: 50px;
            justify-content: space-between;
        }

        &>li {
            @media(min-width: 768px) {
                &.active a {
                    &:before {
                        opacity: 1;
                    }
                }
            }

            @media(max-width: 767px) {
                &.active a {
                    color: #00B4D8;
                }
            }
        }

        &>li>a {
            text-decoration: none;

            @media(max-width: 767px) {
                padding: 10px 0;

                &:active {
                    color: #00B4D8;
                }
            }

            display:flex;
            align-items: center;

            img {
                margin-right: 10px;
            }

            font-weight: bold;
            text-transform: uppercase;
            color: #525252;

            @media(min-width: 768px) {
                height: 100%;
                position: relative;

                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: -2px;
                    left: -10px;
                    right: -10px;
                    height: 3px;
                    background: #00B4D8;
                    opacity: 0;
                    transition: opacity 0.3s ease;
                }

                &:hover,
                &:focus {
                    color: #00B4D8;

                    &:before {
                        opacity: 1;
                    }
                }
            }
        }
    }
}

.header-city-wrap {
    @media(max-width: 767px) {
        margin-top: 7px;
    }
}

.header-items-row {
    align-items: center;

    @media(min-width: 992px) {
        height: 90px;
    }

    @media(min-width:768px) and (max-width:1199px) {
        [class^="col-"] {
            flex-basis: auto;
            width: auto;
            max-width: inherit;
        }
    }

    @media(min-width:768px) and (max-width:991px) {
        padding: 10px 0;
    }

    @media(min-width:992px) and (max-width:1199px) {
        justify-content: space-between;
    }

}

.header-logo-col {
    @media(max-width: 767px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }


}

.logo {
    @media(max-width: 575px) {
        min-width: 46px;
    }
}

.mobile-menu-toggler {
    @media(max-width: 767px) {
        padding: 0;
        border: 0;
        background: transparent;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-width: 40px;
        height: 38px;
        border-radius: 3px;
        border: 1px solid #333;
        margin-left: 6px;

        span {
            height: 2px;
            width: 28px;
            background: #333;

            &:not(:last-child) {
                margin-bottom: 6px;
            }
        }

        &:active {
            background: #00B4D8;
            border-color: #00B4D8;

            span {
                background: #fff;
            }
        }

    }
}

.header-city-col {
    @media(max-width: 767px) {
        margin-bottom: 5px;
        display: flex;
        justify-content: space-between;
        align-content: center;
    }

}

.mobile-phone-i {
    @media(max-width: 767px) {
        display: flex;
    }
}

.mobile-search-toggler {
    @media(max-width: 767px) {
        display: flex;
        padding: 0;
        border: 0;
        background: transparent;

        img {
            height: 18px;
        }
    }
}

.header-current-city {
    @media(min-width: 992px) {
        font-size: 16px;
    }

    display: flex;
    color:#A3A3A3;

    b {
        @media(max-width: 767px) {
            margin-left: 3px;
        }

        @media(min-width:768px) {
            margin-left: 5px;
        }

        font-weight:700;

        @media(max-width:991px) {
            font-size: 16px;
        }

        @media(min-width:992px) {
            font-size: 20px;
        }

        color: #333333;

    }

    align-items: center;

    img {
        @media(max-width: 767px) {
            margin-right: 2px;
        }

        @media(min-width: 768px) and (max-width: 991px) {
            margin-right: 4px;
        }

        @media(max-width: 991px) {
            height: 11px;
        }

        @media(min-width: 992px) {
            margin-right: 10px;
        }

    }
}

.header-basket-col {}

.header-basket-wrap {
    position: relative;
    font-size: 16px;
    display: flex;
    align-items: center;
    color: #333333;

    .counter {
        position: absolute;

        @media(max-width: 767px) {
            right: -19px;
        }

        @media(min-width: 768px) {
            right: -17px;
        }

        top:-5px;
        line-height: 1;
        color:#fff;
        padding: 3px 5px;
        border-radius: 3px;
        font-size: 13px;
        font-weight:700;
        background: #F18E3F;
    }

    @media(min-width: 1200px) {
        margin-left: 12px;
    }

    a {
        color: #333;

        &:hover,
        &:focus {
            color: #00B4D8;
        }
    }

    img {
        @media(min-width: 768px) {
            margin-right: 10px;
        }
    }
}

.header-login-col {
    display: flex;
    align-items: center;

    @media(max-width: 767px) {
        margin-bottom: 5px;
    }

    @media(min-width: 768px) and (max-width: 991px) {
        margin-top: 10px;
    }
}

.header-login-wrap {

    font-size: 16px;

    a {
        color: #333333;

        &:first-of-type {
            margin-right: 5px;
        }

        &:last-of-type {
            margin-left: 5px;

        }

        &:hover,
        &:focus {
            color: #00B4D8;
        }
    }

    img {
        margin-right: 10px;

        @media(min-width: 1200px) {
            margin-left: 34px;
        }
    }
}

.header-phones-col {
    @media(min-width: 768px) {
        justify-content: flex-end;
    }

    @media(min-width: 768px) and (max-width: 991px) {
        margin-top: 10px;
    }
}

.header-phones-wrap {
    display: flex;
    align-items: center;

    img {
        min-width: 16px;
        margin-right: 10px;
    }
}

.header-phones {
    display: flex;

    @media(min-width: 992px) {
        flex-direction: column;
    }

    a {
        font-size: 16px;
        line-height: 1.4;
        font-weight: 700;
        color: #525252;

        &:hover,
        &:focus {
            color: #00B4D8;

            span {
                color: #00B4D8;
            }
        }

        span {
            font-weight: 400;
            color: #A3A3A3;
        }

        @media(min-width: 576px) and (max-width: 991px) {
            &:not(:last-child) {
                margin-right: 15px;
            }
        }
    }
}

.header-bottom-section {
    background: #f6f6f7;

    @media(max-width: 767px) {
        padding: 10px 0;
        display: none;
    }

    @media(min-width:1200px) {
        padding: 40px 0 30px;
    }

    @media(min-width:768px) and (max-width:1199px) {
        padding: 20px 0 20px;
    }
}

.header-search-form {
    position: relative;

    @media(min-width: 1200px) {
        margin-bottom: 40px;
    }

    @media(min-width:992px) and (max-width:1199px) {
        margin-bottom: 20px;
    }
}

.header-serach-field {
    @media(max-width: 767px) {
        font-size: 18px;
    }

    @media(min-width: 768px) {
        font-size: 20px;
    }

    font-weight: 700;
    color:#333;

    @media(max-width:767px) {
        height: 35px;
    }

    @media(min-width:768px) {
        height: 60px;
    }

    border-color:#D1D1D1;
    border-radius: 3px;

    @media(max-width:767px) {
        padding: 5px 40px 5px 5px;
    }

    @media(min-width:768px) {
        padding: 10px 135px 10px 28px;
    }
}

.header-serach-field::-webkit-input-placeholder {

    /* Chrome/Opera/Safari */
    @media(min-width: 768px) {
        font-size: 20px;
    }

    @media(max-width: 767px) {
        font-size: 18px;
    }

    color: #A3A3A3;
    opacity: 1;
    font-weight: 400;
}

.header-serach-field::-moz-placeholder {

    /* Firefox 19+ */
    @media(min-width: 768px) {
        font-size: 20px;
    }

    @media(max-width: 767px) {
        font-size: 18px;
    }

    color: #A3A3A3;
    opacity: 1;
    font-weight: 400;
}

.header-serach-field:-ms-input-placeholder {

    /* IE 10+ */
    @media(min-width: 768px) {
        font-size: 20px;
    }

    @media(max-width: 767px) {
        font-size: 18px;
    }

    color: #A3A3A3;
    opacity: 1;
    font-weight: 400;
}

.header-serach-submit-btn {
    color: #fff;
    font-weight: 700;
    text-transform: uppercase;
    position: absolute;

    @media(min-width: 768px) {
        width: 119px;
        height: 50px;
        top: 5px;
        right: 5px;
        border-radius: 3px;
    }

    @media(max-width:767px) {
        top: 0;
        right: 0;
        font-size: 0;
        width: 35px;
        height: 35px;
        border-radius: 0 3px 3px 0;
    }

    background: #00B4D8;

    padding:0;
    border:0;
    transition: background 0.3s ease;

    img {
        @media(min-width: 768px) {
            margin-right: 12px;
        }

        @media(max-width: 767px) {
            height: 17px;
        }
    }

    &:hover,
    &:focus {
        background: #F18E3F;
    }

    &:active {
        background: #E86700;
    }
}

.header-cat-row {
    @media(min-width: 992px) and (max-width:1199px) {
        margin: 0 -5px;
    }
}

.header-cat-col {
    @media(min-width: 992px) and (max-width:1199px) {
        padding: 0 5px;
    }
}

.header-cat-item {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    background: #fff;
    border: 1px solid #D1D1D1;
    border-radius: 3px;
    height: 160px;
    transition: border-color 0.3s ease;

    &:hover,
    &:focus {
        text-decoration: none;
        border-color: #00B4D8;

        .header-cat-item-title {
            color: #00B4D8;
        }
    }
}

.header-cat-item-pic-wrap {
    width: 110px;
    min-height: 90px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;

    img {
        max-width: 100%;
        max-height: 100%;
    }
}

.header-cat-item-title {
    display: flex;
    text-align: center;
    align-items: center;
    font-weight: bold;
    color: #333333;
    transition: color 0.3s ease;
    min-height: 60px;
    line-height: 1;
}

/*Home-slider*/
.main-home-slider-section {
    @media(max-width: 767px) {
        padding: 5px 0 15px;
    }

    @media(min-width: 768px) {
        padding: 25px 0;
    }
}

.main-home-slider {
    display: none;

    .slick-arrow {
        width: 20px;
        height: 40px;
        background: red;
        z-index: 1;
        transition: opacity 0.3s ease, margin-top 0.3s ease;

        &:hover,
        &:focus {
            opacity: 0.75;
        }

        &:active {
            margin-top: 5px;
        }

        &:before {
            display: none;
        }
    }

    .slick-prev {
        left: 24px;
        background: url(../img/i/back.svg) no-repeat 50%;
    }

    .slick-next {
        right: 24px;
        background: url(../img/i/forward.svg) no-repeat 50%;
    }

    .slick-dots {
        display: flex;
        justify-content: center;
        bottom: 20px;

        li {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 11px;
            height: 11px;
            border-radius: 100%;

            &.slick-active,
            &:hover {
                background: #00B4D8;

                button {
                    background: #fff;
                }
            }

        }

        button {
            width: 5px;
            height: 5px;
            border-radius: 100%;
            background: #C4C4C4;
            padding: 0;
            box-sizing: content-box;

            &:before {
                display: none;
            }
        }
    }
}

.main-home-slider.slick-initialized {
    display: block;
    margin-bottom: 0;
}

.main-home-slider-item {
    position: relative;
    overflow: hidden;

    @media(min-width: 1200px) {
        height: 410px;
    }

    @media(min-width:992px) and (max-height: 1199px) {
        height: 370px;
    }

    @media(min-width:768px) and (max-height:991px) {
        height: 300px;
    }

    @media(min-width:576px) and (max-height:767px) {
        height: 230px;
    }

    @media(max-width:575px) {
        height: 180px;
    }

    border-radius: 3px;

    img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        @media(max-width: 575px) {
            height: 100%;
            max-width: inherit;
        }

        @media(min-width: 576px) {
            min-width: 100%;

            min-height: 100%;
            max-width: 130%;
        }
    }
}

/*Home*/
.home-catalog-section {
    @media(min-width: 1200px) {
        padding: 30px 0 120px;
    }

    @media(max-width: 575px) {
        padding-bottom: 25px;
    }

    @media(min-width: 576px) and (max-width:767px) {
        padding: 15px 0 25px;
    }

    @media(min-width:768px) and (max-width: 1199px) {
        padding: 30px 0 40px;
    }
}

.gray-section {
    background: #f6f6f7;
}

.home-catalog-section-header {
    border-bottom: 1px solid #D1D1D1;

    @media(max-width: 767px) {
        margin-bottom: 15px;
    }

    @media(min-width: 768px) and (max-width:991px) {
        margin-bottom: 30px;
    }

    @media(min-width:992px) {
        margin-bottom: 70px;
    }
}

.home-cat-header-nav {
    @media(max-width: 575px) {
        flex-direction: column;
    }

    li {
        &:not(:last-child) {
            @media(min-width: 768px) and (max-height:991px) {
                margin-right: 30px;
            }

            @media(min-width:992px) and (max-height: 1199px) {
                margin-right: 50px;
            }

            @media(min-width:1200px) {
                margin-right: 100px;
            }
        }

        a {
            position: relative;
            display: block;
            font-weight: bold;

            @media(min-width: 768px) {
                font-size: 20px;
            }

            @media(max-width: 767px) {
                font-size: 18px;
            }

            color: #525252;

            @media(max-width: 575px) {
                padding: 10px 15px;
            }

            @media(min-width: 576px) {
                padding: 0 15px 10px;
            }

            text-decoration: none;

            &.active,
            &:hover {
                &:before {
                    content: '';
                    position: absolute;
                    height: 3px;
                    left: 0;
                    right: 0;
                    bottom: -2px;
                    background: #00B4D8;
                }
            }
        }
    }
}

.home-catalog-tab-content {
    margin-bottom: 50px;
}

.home-big-banner {
    @media(min-width: 1200px) {
        margin-bottom: 118px;
    }

    @media(max-width:1199px) {
        margin-bottom: 40px;
    }
}

.home-preims-col {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media(max-width: 575px) {
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }

    @media(min-width:576px) and (max-width:767px) {

        &:first-child,
        &:nth-child(2) {
            margin-bottom: 30px;
        }
    }
}

.home-preims-i {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    width: 100px;
    height: 100px;
    border-radius: 100%;

    @media(min-width: 768px) {
        margin-bottom: 40px;
    }

    @media(max-width: 767px) {
        margin-bottom: 15px;
    }
}

.home-preims-title {
    font-size: 16px;
    text-align: center;
}

.home-brands-section {
    @media(max-width: 767px) {
        padding: 15px 0 50px;
    }

    @media(min-width:768px) and (max-width: 1199px) {
        padding: 30px 0 80px;
    }

    @media(min-width: 1200px) {
        padding: 75px 0 150px;
    }
}

.section-title {
    font-weight: bold;

    @media(min-width: 992px) {
        font-size: 50px;
    }

    @media(max-width:767px) {
        font-size: 25px;
    }

    @media(min-width:768px) and (max-width: 991px) {
        font-size: 30px;
    }

    text-align: center;

    @media(max-width: 1199px) {
        margin-bottom: 20px;
    }

    @media(min-width: 1200px) {
        margin-bottom: 40px;
    }
}

.home-brands-carousel {

    display: none;

    &.slick-initialized {
        display: block;

        @media(min-width: 992px) {
            margin: 0 80px;
        }

        @media(max-width: 991px) {
            margin-bottom: 0;
        }

    }

    .slick-arrow {
        width: 20px;
        height: 40px;
        background: red;
        z-index: 1;
        transition: opacity 0.3s ease, margin-top 0.3s ease;

        &:hover,
        &:focus {
            opacity: 0.75;
        }

        &:active {
            margin-top: 5px;
        }

        &:before {
            display: none;
        }
    }

    .slick-prev {
        left: -55px;
        background: url(../img/i/back.svg) no-repeat 50%;
    }

    .slick-next {
        right: -55px;
        background: url(../img/i/forward.svg) no-repeat 50%;
    }

    .slick-dots {
        display: flex;
        justify-content: center;

        @media(max-width: 767px) {
            bottom: -30px;
        }

        @media(min-width:768px) and (max-width: 1199px) {
            bottom: -45px;
        }

        @media(min-width: 1200px) {
            bottom: -55px;
        }

        li {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 11px;
            height: 11px;
            border-radius: 100%;

            &.slick-active,
            &:hover {
                background: #00B4D8;

                button {
                    background: #fff;
                }
            }

        }

        button {
            width: 5px;
            height: 5px;
            border-radius: 100%;
            background: #C4C4C4;
            padding: 0;
            box-sizing: content-box;

            &:before {
                display: none;
            }
        }
    }
}

.home-brands-car-item.slick-slide {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.home-brands-item-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 166px;
    height: 80px;
    margin-bottom: 20px;

    img {
        max-width: 100%;
        max-height: 100%;
    }
}

.home-brands-item-name {
    font-weight: bold;
    font-size: 20px;
}

.home-brands-item-type {
    font-style: italic;
}

/*Content*/
img {
    max-width: 100%;
}

h1 {
    font-weight: bold;
    font-size: 50px;
    text-align: center;

    @media(max-width: 767px) {
        margin-bottom: 10px;
    }

    @media(min-width: 768px) and (max-width: 991px) {
        margin-bottom: 15px;
    }

    @media(min-width: 992px) {
        margin-bottom: 30px;
    }

    @media(max-width: 991px) {
        font-size: 26px;
    }
}

h2 {
    font-size: 40px;

    @media(max-width: 991px) {
        font-size: 22px;
    }
}

h3 {
    font-size: 36px;

    @media(max-width: 991px) {
        font-size: 18px;
    }
}

h4 {
    font-size: 32px;

    @media(max-width: 991px) {
        font-size: 16px;
    }
}

h5 {
    font-size: 28px;

    @media(max-width: 991px) {
        font-size: 15px;
    }
}

h6 {
    font-size: 24px;

    @media(max-width: 991px) {
        font-size: 14px;
    }
}

h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
}

.content-section:not(.ending-wide-section)+.site-footer {
    @media(max-width: 991px) {
        margin-top: 30px;
    }

    @media(min-width: 992px) {
        margin-top: 90px;
    }
}

.ending-wide-section .gray-section:last-child {
    @media(max-width: 991px) {
        padding-bottom: 30px;
    }

    @media(min-width: 992px) {
